import React, { Component } from 'react';var divStyle = {    width: '300px',    height: '680px',    display : 'inline-block',    marginRight : '5px'};var divStyle_Right = {    width: '800px',    height: '680px',    display : 'inline-block',    marginLeft : '5px'};class GridMain extends Component {  render(){    return (      <div>          <br/>          <div id="realgrid" style={divStyle}></div>          <div id="realgrid_Right" style={divStyle_Right}></div>      </div>    );  }}; /*{ <br/>          <button type="button" class="btn btn-primary btn-xs" id="btnSearch">데이터 조회</button>          }*/export default GridMain;